/* Reset and base styles  */

* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

/* Links */

a,
a:link,
a:visited {
  color: inherit;
  text-decoration: none;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

/* Common */

aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
}

ul,
ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button,
input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

body {
  font-family: sans-serif;
  font-size: 16px;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
}

.container {
  width: 100%;
  padding: 20px 0;
  background-color: #e6e6e6;
}

.container_my {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  row-gap: 1rem;
  margin: 0 auto;
}

.container_bs {
  margin: 50px auto 0;
}

.button {
  display: block;
  width: 200px;
  height: 50px;
  margin: 20px auto;
  border-radius: 3px;
  background-color: #ac11ff;
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  transition: 200ms;
}

.button:hover {
  background-color: #8b06d3;
}

.pentagon {
  width: 250px;
  height: 250px;
  border: 1px solid #000;
}

.pentagon__image {
  transform-origin: center center 0;
  transition: 200ms;
}

.pentagon__image:hover {
  transform: scale(1.1);
}

.title {
  padding: 20px 0;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: 1100px) {
  .container_my {
    grid-template-columns: repeat(3, 33.33%);
  }
}

@media (max-width: 720px) {
  .container_my {
    grid-template-columns: repeat(2, 50%);
  }
}

@media (max-width: 420px) {
  .container_my {
    grid-template-columns: repeat(1, 100%);
  }
}